import React from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLink } from "@fortawesome/free-solid-svg-icons";

const ProjectCard = ({
	projectStatus,
	image,
	projectName,
	logo,
	projectDesc,
	projectLink,
	techStack,
	tooltip,
}: {
	projectStatus: string;
	image?: JSX.Element;
	projectName: string;
	logo?: JSX.Element;
	projectDesc: string;
	projectLink: string;
	techStack?: Array<any>;
	tooltip?: string;
}): JSX.Element => {
	return (
		// <div className="column is-half-tablet is-full-mobile is-one-third-desktop">
		<div className="card">
			<div className="card-content">
				<article className="media">
					<div className={`media-left`}>
						{logo ? (
							<span className="icon is-large">{logo}</span>
						) : image ? (
							<figure className="image is-64x64">
								{image}
							</figure>
						) : (
							<></>
						)}
					</div>
					<div className="media-content no-overflow">
						<p className="title is-5">
							<a
								href={projectLink}
								target="_blank"
								rel="noopener noreferrer"
							>
								{projectName}
								<FontAwesomeIcon icon={faLink} size="lg" className="icon is-small" />
							</a>
							{projectStatus === "complete" ? (
								<span className="tag is-status is-success is-light is-rounded">
									Complete
								</span>
							) : projectStatus === "incomplete" ? (
								<span className="tag is-status is-warning is-light is-rounded">
									In Progress
								</span>
							) : projectStatus === "inactive" ? (
								<span className="tag is-status is-danger is-light is-rounded">
									<span
										className="has-tooltip-left has-tooltip-danger has-tooltip-multiline"
										data-tooltip={tooltip}
									>
										Inactive
									</span>
								</span>
							) : (
								<></>
							)}
						</p>
					</div>
				</article>

				<p>{projectDesc}</p>

				{techStack ? (
					<footer className="tags has-text-centered ts">
						{techStack.map((tech) => (
							<span className="tag" key={tech.name}>
								<span>
									<img
										src={tech.logo}
										alt={`${tech.name} Logo`}
									/>
								</span>
								{tech.name}
							</span>
						))}
					</footer>
				) : (
					<></>
				)}
			</div>
		</div>
	);
};

export default ProjectCard;
